<template>
    <div class="catalog">
        <div class="catalog-icon" v-for="item of catalogList" :key="item.id">
            <img :src="item.imgUrl" alt="图标" class="icon-img" :class="[item.color]">
            <p class="icon-desc">{{item.desc}}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'homeCatalog',
    props: {
        catalogList: Array
    },
    data() {
        return {
            red:'red',
            orange:'orange',
            green:'green',
            blue:'blue'
            // catalogList:[{
            //         id:"0001",
            //         imgUrl:data:"image/png;base64,",
            //         desc:"本地热推",
            //         color:"red"
            //     },
            //     {
            //         id:"0002",
            //         imgUrl:"data:image/png;base64,",
            //         desc:"亲子游",
            //         color:"green"
            //     },
            //     {
            //         id:"0003",
            //         imgUrl:"data:image/png;base64,",
            //         desc:"温泉预售",
            //         color:"blue"
            //     },
            //     {
            //         id:"0004",
            //         imgUrl:"data:image/png;base64,",
            //         desc:"自然风光",
            //         color:"pink"
            //     }]
        }
    }
}
</script>
<style scoped>
    .catalog{
        display: flex;
        width: 100%;
        padding: 15px 0;
        justify-content: space-between;
        align-content: center;
    }
    .catalog-icon{
        display: flex;
        flex: 1;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .icon-img{
        width: 60%;
        margin-bottom: 10px;
         border-radius: 50%;
    }
    .red{
      background:#fd6266
    }
    .orange{
      background:#fda424
    }
    .green
      {background:#6dcf16}
    .blue
      {background:#41a9f2}
</style>


